@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;800&family=Yeseva+One&display=swap');
body {
  margin: 0;
  background-color: rgb(29, 29, 29);
  color: #fff;
  font-weight: normal;
  font-family: 'Nunito', sans-serif;
}
html, body{
  width: 100%;
  height:100%;
}
html{
  height: 100vh;
}
 
a{
  cursor: pointer;
  user-select: none;
}
.backdrop{
  display: block;
  position: absolute;
  left:0; top:0;
  height:100%;
  width:100%;
  z-index: 0;
}
.content{
  display: block;
  position: relative;
  height: 100%;
  width:100%;
  z-index: 1;
}