$size: 500px;
$profileSize: $size/5;
$iconSize: $size/6;

.film-roll-container {
    display: block;
    position: relative;
    flex-direction: column;
    width: $size;
    height: $size;
  
    > {
      * {
        flex: 1;
        display: flex;
        position: absolute;
        height: 25%;
        width: 80%;
        top: 40%;
        left: 10%;
      }
  
      .above-profile, .below-profile {
        justify-content: space-between;
        align-items: center;
        z-index: 1;
      }
  
      .profile {
        align-items: center;
        justify-content: center;
        transform: scale(1.5);
  
        img {
          position: absolute;
          z-index: 1;
          box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.5);
          width: $profileSize;
          border-radius: 50%;
          
        }
  
        .line {
          position: absolute;
          height: 0;
          width: 75%;
          top: 50%;
          left: 12.5%;
          z-index: 0;
          border: 2px dashed rgba(0, 0, 0, 0);
  
          &.ninety {
            transform: rotate(90deg);
          }
        }
      }
  
      .below-profile {
        transform: rotate(90deg);
      }
    }
  
    i {
      display: block;
      box-sizing: border-box;
      background-repeat: no-repeat;
      width: $iconSize;
      height: $iconSize;
      border-radius: 50%;
    }
  }
  
  /* Images */
  
  .rotate, .line.zero {
    animation: rotate 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite reverse forwards;
  }
  
  .rotate-ninety, .line.ninety {
    animation: rotate-ninety 30s cubic-bezier(0.61, 0.81, 0.41, 0.21) infinite reverse forwards;
  }
  
  .rotate > {
    .write {
      animation: write-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards;
    }
  
    .prod {
      animation: prod-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards;
    }
  }
  
  .rotate-ninety > {
    .pre-prod {
      animation: pre-prod-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards;
    }
  
    .edit {
      animation: edit-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards;
    }
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0);
      transform-origin: center;
    }
  
    50% {
      transform: rotate(180deg);
      transform-origin: center;
    }
  
    100% {
      transform: rotate(360deg);
      transform-origin: center;
    }
  }
  
  @keyframes rotate-ninety {
    0% {
      transform: rotate(90deg);
      transform-origin: center;
    }
  
    50% {
      transform: rotate(270deg);
      transform-origin: center;
    }
  
    100% {
      transform: rotate(450deg);
      transform-origin: center;
    }
  }
  
  @keyframes write-scale {
    0% {
      transform: scale(1.3);
      transform-origin: center;
    }
  
    50% {
      transform: scale(0.7);
      transform-origin: center;
    }
  
    100% {
      transform: scale(1.3);
      transform-origin: center;
    }
  }
  
  @keyframes prod-scale {
    0% {
      transform: scale(0.7) rotate(-180deg);
      transform-origin: center;
    }
  
    50% {
      transform: scale(1.3) rotate(-180deg);
      transform-origin: center;
    }
  
    100% {
      transform: scale(0.7) rotate(-180deg);
      transform-origin: center;
    }
  }
  
  @keyframes pre-prod-scale {
    25% {
      transform: scale(1.3);
      transform-origin: center;
    }
  
    75% {
      transform: scale(0.7);
      transform-origin: center;
    }
  }
  
  @keyframes edit-scale {
    0% {
      transform: rotate(-180deg);
    }
  
    25% {
      transform: scale(0.7) rotate(-180deg);
      transform-origin: center;
    }
  
    75% {
      transform: scale(1.3) rotate(-180deg);
      transform-origin: center;
    }
  
    100% {
      transform: rotate(-180deg);
    }
  }