.section.editstuff{
    display: flex;
    box-sizing: border-box;
    padding: 25px;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    .title-block{
        padding: 0 25px;
        position: relative;
        float: right;
        transform: translateY(-50%);
    }
    .pretitle{
        font-size: 2rem;
        margin: 5px;
    }
    .title{
        display: inline-block;
        color: #222;
        background: #fff;
        margin: 5px;
        padding: 10px;
        font-size: 5rem;
        font-family: 'Yeseva One', serif;
        font-weight: normal !important;
        text-transform: uppercase;
    }
    .subtitle{
        display: inline-block;
        margin: 5px;
        padding: 0 3px;
        font-size: 2rem;
        font-family: 'Nunito', sans-serif;
        font-weight: 100;
    }

    .pretitle{
        font-size: 1.2rem;
        text-align: right;
    }
}