@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;800&family=Yeseva+One&display=swap);
body{margin:0;background-color:#1d1d1d;color:#fff;font-weight:normal;font-family:'Nunito', sans-serif}html,body{width:100%;height:100%}html{height:100vh}a{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.backdrop{display:block;position:absolute;left:0;top:0;height:100%;width:100%;z-index:0}.content{display:block;position:relative;height:100%;width:100%;z-index:1}

.film-roll-container{display:block;position:relative;flex-direction:column;width:500px;height:500px}.film-roll-container>*{flex:1 1;display:flex;position:absolute;height:25%;width:80%;top:40%;left:10%}.film-roll-container>.above-profile,.film-roll-container>.below-profile{justify-content:space-between;align-items:center;z-index:1}.film-roll-container>.profile{align-items:center;justify-content:center;transform:scale(1.5)}.film-roll-container>.profile img{position:absolute;z-index:1;box-shadow:0px 0px 15px -3px rgba(0,0,0,0.5);width:100px;border-radius:50%}.film-roll-container>.profile .line{position:absolute;height:0;width:75%;top:50%;left:12.5%;z-index:0;border:2px dashed rgba(0,0,0,0)}.film-roll-container>.profile .line.ninety{transform:rotate(90deg)}.film-roll-container>.below-profile{transform:rotate(90deg)}.film-roll-container i{display:block;box-sizing:border-box;background-repeat:no-repeat;width:83.33333px;height:83.33333px;border-radius:50%}.rotate,.line.zero{animation:rotate 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite reverse forwards}.rotate-ninety,.line.ninety{animation:rotate-ninety 30s cubic-bezier(0.61, 0.81, 0.41, 0.21) infinite reverse forwards}.rotate>.write{animation:write-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards}.rotate>.prod{animation:prod-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards}.rotate-ninety>.pre-prod{animation:pre-prod-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards}.rotate-ninety>.edit{animation:edit-scale 30s cubic-bezier(0.41, 0.21, 0.61, 0.81) infinite forwards}@keyframes rotate{0%{transform:rotate(0);transform-origin:center}50%{transform:rotate(180deg);transform-origin:center}100%{transform:rotate(360deg);transform-origin:center}}@keyframes rotate-ninety{0%{transform:rotate(90deg);transform-origin:center}50%{transform:rotate(270deg);transform-origin:center}100%{transform:rotate(450deg);transform-origin:center}}@keyframes write-scale{0%{transform:scale(1.3);transform-origin:center}50%{transform:scale(0.7);transform-origin:center}100%{transform:scale(1.3);transform-origin:center}}@keyframes prod-scale{0%{transform:scale(0.7) rotate(-180deg);transform-origin:center}50%{transform:scale(1.3) rotate(-180deg);transform-origin:center}100%{transform:scale(0.7) rotate(-180deg);transform-origin:center}}@keyframes pre-prod-scale{25%{transform:scale(1.3);transform-origin:center}75%{transform:scale(0.7);transform-origin:center}}@keyframes edit-scale{0%{transform:rotate(-180deg)}25%{transform:scale(0.7) rotate(-180deg);transform-origin:center}75%{transform:scale(1.3) rotate(-180deg);transform-origin:center}100%{transform:rotate(-180deg)}}

.section.intro{display:flex;box-sizing:border-box;padding:25px;flex-direction:row;justify-content:space-around;align-items:center}.section.intro .introduction .pretitle{font-size:2rem;margin:0}.section.intro .introduction .title{margin:5px 25px 0 0;font-size:5rem;font-family:'Yeseva One', serif;font-weight:normal !important;text-transform:uppercase}.section.intro .introduction .subtitle{color:#000;background:#fff;padding:0 5px;margin:25px 0;font-size:2rem;font-family:'Nunito', sans-serif;font-weight:100}.section.intro .introduction .subcomment{margin:0;opacity:0.7}.section.intro .introduction .social-media{list-style-type:none;display:flex;align-items:flex-start;padding-left:0}.section.intro .introduction .social-media li{position:relative;display:block;margin:10px}.section.intro .introduction .social-media li a{display:flex;position:relative;height:75px;width:75px;box-sizing:border-box;align-items:center;justify-content:center;border-radius:50%;padding:5px;overflow:hidden}.section.intro .introduction .social-media li a img{position:relative;height:40px;z-index:1}.section.intro .introduction .social-media li a .backdrop{position:absolute;width:100%;height:100%;left:0;top:0;z-index:0;opacity:0;transform:scale(0.93);transition:all 0.25s ease-in;border-radius:50%}.section.intro .introduction .social-media li a:hover .backdrop{transform:scale(1);opacity:0.5}.section.intro .tooltip-body{display:flex;align-items:center;justify-content:space-between;z-index:1;box-sizing:border-box}.section.intro .mail-label{font-size:0.7rem;font-weight:800;font-family:'Nunito', sans-serif;color:#333;text-transform:uppercase;background:transparent;border:none;padding:5px;width:18.5em}.section.intro #addressRef:disabled{display:none}.section.intro #copyLabel{color:#f1ff71;text-align:center;text-transform:uppercase;font-size:.65rem;font-weight:800;position:relative;display:flex;flex-direction:column}.section.intro .frontdrop{display:flex;background:#fff;align-items:center;justify-content:center;position:absolute;text-align:center;width:100%;height:100%;z-index:2;transition:0.2s ease;opacity:0;left:0;top:0;font-weight:800;color:#333}.section.intro .copied .frontdrop{opacity:1}.section.intro *::selection{background:#333;color:#fff}

.section.shortfilms{display:flex;box-sizing:border-box;padding:25px;flex-direction:row;justify-content:space-around;align-items:center}.section.shortfilms .title-block{padding:0 25px;display:inline-block;transform:translateY(-50%)}.section.shortfilms .pretitle{font-size:2rem;margin:5px;text-align:right}.section.shortfilms .title{display:inline-block;color:#222;background:#fff;margin:5px;padding:10px;font-size:4.5rem;font-family:'Yeseva One', serif;font-weight:normal !important;text-transform:uppercase}.section.shortfilms .subtitle{display:inline-block;margin:5px;padding:0 3px;font-size:2rem;font-family:'Nunito', sans-serif;font-weight:100}

.section.editstuff{display:flex;box-sizing:border-box;padding:25px;flex-direction:row;justify-content:space-around;align-items:center}.section.editstuff .title-block{padding:0 25px;position:relative;float:right;transform:translateY(-50%)}.section.editstuff .pretitle{font-size:2rem;margin:5px}.section.editstuff .title{display:inline-block;color:#222;background:#fff;margin:5px;padding:10px;font-size:5rem;font-family:'Yeseva One', serif;font-weight:normal !important;text-transform:uppercase}.section.editstuff .subtitle{display:inline-block;margin:5px;padding:0 3px;font-size:2rem;font-family:'Nunito', sans-serif;font-weight:100}.section.editstuff .pretitle{font-size:1.2rem;text-align:right}

#root,.section{display:block;position:relative;height:100%}.page{height:100%}.section{position:relative;z-index:2}

