.section.intro{
    display: flex;
    box-sizing: border-box;
    padding: 25px;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    .introduction{
        .pretitle{
            font-size: 2rem;
            margin:0;
        }
        .title{
            margin: 5px 25px 0 0;
            font-size: 5rem;
            font-family: 'Yeseva One', serif;
            font-weight: normal !important;
            text-transform: uppercase;
        }
        .subtitle{
            color: #000;
            background: #fff;
            padding: 0 5px;
            margin: 25px 0;
            font-size: 2rem;
            font-family: 'Nunito', sans-serif;
            font-weight: 100;
        }
        .subcomment{
            margin: 0;
            opacity: 0.7;
        }
        .social-media{
            list-style-type: none;
            display: flex;
            align-items: flex-start;
            padding-left: 0;
            li{
                position: relative;
                display: block;
                margin: 10px;
                a{
                    display: flex;
                    position: relative;
                    height: 75px;
                    width: 75px;
                    box-sizing: border-box;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    padding: 5px;
                    overflow: hidden;
                   img{
                      position: relative;
                      height: 40px;
                      z-index: 1;
                   }
                   .backdrop{
                       position: absolute;
                       width:100%;
                       height:100%;
                       left:0; top:0;
                       z-index: 0;
                       opacity: 0;
                       transform: scale(0.93);
                       transition: all 0.25s ease-in;
                       border-radius: 50%;
                   }

                   &:hover{
                       .backdrop{
                           transform: scale(1);
                           opacity: 0.5;
                       }
                   }
                   
                }
            }
            
        }
    }
    .tooltip-body{
        display: flex;
        align-items: center;
        justify-content: space-between;
        z-index: 1;
        box-sizing: border-box;
        
    }

    .mail-label{
        font-size: 0.7rem;
        font-weight: 800;
        font-family: 'Nunito', sans-serif;
        color: #333;
        text-transform: uppercase;
        background: transparent;
        border: none;
        padding: 5px;
        width: 18.5em;
    }
    #addressRef:disabled{
        display: none;
    }
    #copyLabel{
        color: rgb(241, 255, 113);
        text-align: center;
        text-transform: uppercase;
        font-size: .65rem;
        font-weight: 800;
        position: relative;
        display: flex;
        flex-direction: column;
        
    }
    .frontdrop{
        display: flex;
        background: #fff;
        align-items: center;
        justify-content: center;
        position: absolute;
        text-align: center;
        width:100%;
        height:100%;
        z-index: 2;
        transition: 0.2s ease; 
        opacity: 0;
        left:0; top:0;
        font-weight: 800;
        color:#333
    }
    .copied .frontdrop{
        opacity: 1;
    }
    & *::selection {
        background: #333;
        color: #fff;
      } 

}
